.add-cloud {
  height: auto;

  .com-card {
    padding: 0.56rem 0.3rem 1.95rem 0.3rem;
  }

  .com-tit {
    color: #040e34;
    line-height: 1;
    margin-bottom: 0.32rem;

    .tit {
      margin-bottom: 0;
      font-size: 0.18rem;
      font-weight: 500;
      color: #040e34;
      font-family: "Source Han Sans CN";
    }

    .des {
      font-size: 0.12rem;
      color: #9696a3;
      margin-bottom: 0;
      margin-top: 0.1rem;
    }
  }

  .c-active {
    //  border-color: #5C5EE4 !important;
    background: #fff !important;
    box-shadow: 0 0 0.2rem 0.05rem rgba(137, 138, 228, 0.3);

    &.c-hover {
      border-color: transparent !important;
    }

    &::after {
      content: "";
      width: 0.2rem;
      height: 0.2rem;
      position: absolute;
      text-align: center;
      line-height: 0.2rem;
      top: 0;
      right: 0;
      z-index: 2;
      background: url(../../../../assets/images/cloud/check.png) no-repeat;
      background-size: 100%;
    }
  }

  .ant-row {
    margin-bottom: 0.3rem;
  }

  .ant-col {
    margin-bottom: 0.3rem;

    .mask {
      color: #dbdbdd;
      position: relative;

      .price,
      .des {
        color: #dbdbdd;

        span {
          color: #dbdbdd;
        }
      }

      &:hover {
        border-color: #dbdbdd;
      }

      .current {
        width: 0.4rem;
        height: 0.2rem;
        line-height: 0.2rem;
        text-align: center;
        color: #fff;
        position: absolute;
        background-color: #dbdbdd;
        top: 0;
        left: 0;
        font-size: 0.12rem;
      }
    }

    .item {
      border: 1px solid #d9dbe1;
      border-radius: 0.04rem;
      padding: 0.25rem 0.16rem;
      line-height: 0.28rem;
      position: relative;
      cursor: pointer;
      display: flex;
      justify-content: space-between;
      align-items: center;

      &:hover {
        border-color: #5c5ee4 !important;
      }

      .num {
        width: 0.28rem;
        height: 0.28rem;
        line-height: 0.28rem;
        border: 1px solid #d9dbe1;
        display: inline-block;
        text-align: center;
        border-radius: 50%;
        font-size: 0.12rem;
        color: #525666;
        margin-right: 0.1rem;

        &.cur {
          border-color: #5c5ee4;
          color: #5c5ee4;
        }

        &:hover {
          border-color: #5c5ee4;
        }
      }

      .hot {
        width: 0.4rem;
        height: 0.2rem;
        line-height: 0.2rem;
        text-align: center;
        color: #fff;
        position: absolute;
        background-color: #f5a953;
        top: 0;
        left: 0;
        font-size: 0.12rem;
      }
    }
  }

  .package {
    margin-top: -0.1rem;

    .item {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      word-break: break-all;
      height: 2.7rem;
    }

    .price {
      font-size: 0.21rem;
      color: #5c5ee4;

      .p-num {
        font-weight: bold;
        font-size: 0.28rem;
      }
    }

    .des {
      font-size: 0.13rem;
      color: #525666;
      margin-bottom: 0;
      p{
        margin-bottom: 0;
      }
      span {
        font-weight: bold;
        color: #525666;
      }
    }

    .sub-button {
      height: 40px;
      background: #696ae6;
      opacity: 1;
      border-radius: 4px;
      font-size: 14px;
      font-weight: 400;
      border: none;
    }

    .upbtn {
      width: 100%;
      display: flex;
      justify-content: center;
    }
  }

  .ant-tabs-tab {
    padding: 0.1rem 0;
    font-size: 0.16rem;
  }

  .ant-tabs-nav {
    font-size: 0.16rem;
    color: #525666;
    margin-bottom: 0;

    &::before {
      border: none;
    }

    .ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn {
      color: #040e34;
    }

    .ant-tabs-ink-bar {
      background: #5c5ee4;
    }
  }

  .info {
    font-size: 0.12rem;
    color: #9696a3;
    margin: 0.32rem 0 0.2rem;
  }
}

/* cloud-upgrade */
.cloud-upgrade{
  .refund-modal{
    font-size: .14rem;
    color: #040E34;
  }
  .due-time,.total-price{
    line-height: 1;
    margin-bottom: .2rem;
  }
  .modal-footer{
    margin-top: .55rem;
  }
  .white{
    background-color: #fff;
    border: 1px solid #D9DBE1;
  }
  .row-r{
    padding: 0.25rem 0.2rem;
    font-size: 0.13rem;
    color: #9696A3;
    .item{
      width: 100%;
      display: flex !important;
      justify-content: space-between;
      align-items: center;
      .price{
        font-size: .13rem;
        color: #5C5EE4;
        margin-bottom: 0;
      }
    }
    &:last-child{
      color: #040E34;
    }
    .des{
      margin-bottom: 0;
      p{
        margin-bottom: 0;
        line-height: .3rem;
      }
    }
  }
  .day{
    font-size: .14rem;
    color: #9696A3;
    margin: 0 .1rem;
  }
  .total-price{
    .num{
      span{
        font-weight: normal;
        font-size: .14rem;
      }
      display: inline-block;
      font-size: .18rem;
      font-weight: bold;
      color: #FEB63B;
    }
  }
}
@media screen and (max-width: 992px) {
  .add-cloud {
    .total-box {
      padding-left: 0;
    }
  }
}

@media screen and (max-width: 768px) {}
@primary-color: #5C5EE4;