.summary {
    .cloud-card {
        height: 100%;

        .r-notes {
            display: flex;
            flex-direction: row;

            .notes-btn {
                width: .72rem;
                border: 1px solid #5C5EE4;
                background: #FFF;
                color: #5C5EE4;
                border-radius: 4px;
                cursor: pointer;
                display: flex;
                align-items: center;
                justify-content: center;
                margin-left: .1rem;
            }

            .notes-input {
                width: 2rem;
            }
        }

        .renew-btn,
        .stop-btn,
        .un-renew-btn,
        .stoping-btn,
        .quit-stop-btn {
            display: inline-block;
            text-align: center;
            height: 24px;
            border-radius: 4px;
            font-size: 14px;
            margin-left: 8px;
        }

        .renew-btn,
        .stop-btn,
        .quit-stop-btn {
            cursor: pointer;
        }

        .renew-btn {
            width: 44px;
            background: #5C5EE4;
            color: #fff;
        }

        .stop-btn {
            width: 72px;
            border: 1px solid #5C5EE4;
            background: #FFF;
            color: #5C5EE4;
        }

        .un-renew-btn {
            width: 44px;
            background: #F2F2F7;
            opacity: 1;
            border-radius: 4px;
            color: #9696A3;
        }

        .stoping-btn {
            width: 58px;
            border: 1px solid #D9DBE1;
            border-radius: 4px;
            color: #9696A3;
        }

        .quit-stop-btn {
            color: #5C5EE4;
        }

        .err-msg {
            height: auto;
            padding-top: 15px;
            padding-bottom: 15px;
            background: #FFE2E5;
            opacity: 1;
            border-radius: 8px;
            color: #F64E60;
            font-size: 16px;
            display: flex;
            justify-content: center;

            a {
                color: #F64E60;
                font-size: 16px;
                border-bottom: 1px solid;
                text-decoration: solid;
            }
        }

        .info-msg {
            height: auto;
            padding-top: 15px;
            padding-bottom: 15px;
            background: #E1F0FF;
            opacity: 1;
            border-radius: 8px;
            color: #3699FF;
            font-size: 16px;
            display: flex;
            justify-content: center;


            .important {
                font-weight: 500;
            }
        }

    }

    .modal-unopen {
        .modal-title {
            font-size: 18px !important;
            font-weight: 400;
            line-height: 28px;
            color: #040E34;
            margin-bottom: 24px;
        }

        .modal-form {
            margin-top: 4px;

            .ant-row {
                &:nth-child(3) {
                    margin-top: 20px;
                }

                &:nth-child(4) {
                    margin-top: 20px;
                }
            }

            .row-center {
                margin-top: 10px;
            }


            .row-r {
                background: #F2F2F7;

                .item-r-first {
                    padding: 10px;
                }

                // .l-tit,
                .r-text-l {
                    padding-top: 26px;

                    &:last-child {
                        padding-bottom: 26px;
                    }
                }

                .r-text-l {
                    padding-left: 105px;
                    color: #9696A3;
                }

                .ant-select-selector,
                .ant-input {
                    background: #F2F2F7;
                }


            }
        }

        .modal-footer {
            width: 100%;
            justify-content: center;
            font-size: 14px;
            letter-spacing: 0;
            margin-top: 56px;
            display: flex;
            align-items: center;

            .btn-ok,
            .btn-no {
                border-radius: 4px;
                height: 40px;
            }

            .btn-ok {
                background: #5C5EE4;
                color: #ffffff;
            }

            .btn-no {
                border: 1px solid #D9DBE1;
                margin-left: 8px;
            }
        }
    }

    .modal-renew {
        .modal-title {
            font-size: 18px;
            font-weight: 400;
            line-height: 28px;
            color: #040E34;
            margin-bottom: 24px;
        }

        .modal-top {
            height: 110px;
            display: flex;
            flex-direction: row;
            align-items: center;

            .top-l,
            .top-r {
                cursor: pointer;

                &:hover {
                    border-radius: 4px;
                    background: #F2F2F7;
                }
            }

            .top-m {
                width: 100%;
                display: flex;
                justify-content: space-between;
                // flex-direction: row;
                padding-left: 10px;
                padding-right: 10px;
                overflow: hidden;

                .m-card {
                    display: inline-block;
                    width: 180px;
                    height: 110px;
                    border: 1px solid #D9DBE1;
                    opacity: 1;
                    border-radius: 4px;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    cursor: pointer;

                    .cycle {
                        font-size: 16px;
                        color: #9696A3;
                        line-height: 16px;
                        margin-top: 15px;
                    }

                    .money {
                        margin-top: 18px;

                        .money-prefix {
                            font-size: 14px;
                            color: #040E34;
                            line-height: 14px;
                        }

                        .money-num {
                            font-size: 26px;
                            line-height: 0px;
                            font-family: Source Han Sans CN;
                            font-weight: bold;
                            color: #040E34;

                        }
                    }

                    .true-money {
                        color: #9696A3;
                        font-size: 13px;
                        text-decoration: line-through;
                    }
                }

                .active {
                    background: #5C5EE4;

                    .money {

                        .money-prefix,
                        .money-num {
                            color: #FFF !important;
                        }
                    }

                    .cycle,
                    .true-money,
                    .money {
                        color: #FFF !important;
                    }
                }

                .hiden {
                    display: none;
                }
            }

        }

        .modal-middle {
            width: 100%;
            background: #F2F2F7;
            margin-top: 30px;
            display: flex;
            flex-direction: row-reverse;

            .main {
                margin-top: 20px;
                margin-bottom: 20px;
                display: flex;
                flex-direction: column;
                justify-content: flex-end;

                .text {
                    color: #040E34;
                    line-height: 14px;
                    display: flex;
                    justify-content: flex-end;
                    margin-right: 170px;
                }

                .money {
                    margin-top: 12px;
                    line-height: 14px;
                    display: flex;
                    justify-content: flex-end;
                    margin-right: 62px;

                    .pre,
                    .num,
                    .btn {
                        color: #FEB63B;
                    }

                    .pre {
                        font-weight: bold;
                    }

                    .num {
                        font-size: 18px;
                        font-weight: bold;
                    }

                    .btn {
                        margin-left: 31px;
                    }
                }

                .operation {
                    display: flex;
                    flex-direction: row;
                    justify-content: flex-end;
                    margin-top: 10px;
                    margin-right: 62px;
                    padding: 8px;
                    width: 252px;
                    height: 48px;
                    background: #FFFFFF;
                    opacity: 1;
                    border-radius: 4px;

                    .o-input {
                        background: #F2F2F7;
                        border: none;
                    }

                    .o-btn {
                        height: 32px;
                        background: #FEB63B;
                        opacity: 1;
                        border-radius: 4px;
                        color: #FFFFFF;
                        margin-left: 4px;
                    }
                }

                .details {
                    display: flex;
                    flex-direction: column;
                    margin-top: 4px;
                    margin-right: 41px;

                    .d-item {
                        display: flex;
                        justify-content: flex-end;
                        margin-top: 8px;

                        .item-pre {
                            height: 14px;
                            line-height: 14px;
                            color: #FCAF55;
                            margin-right: 4px;
                        }

                        .item-code {
                            font-size: 12px;
                            line-height: 14px;
                            color: #9696A3;
                            margin-right: 10px;
                        }

                        .item-out {
                            width: 14px;
                            height: 14px;
                            background: #FFFFFF;
                            border: 1px solid #D9DBE1;
                            border-radius: 50%;
                            opacity: 1;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            cursor: pointer;

                            .item-in {
                                width: 9px;
                                height: 0px;
                                border: 1px solid #9696A3;
                                opacity: 1;
                            }
                        }
                    }
                }
            }
        }

        .modal-bottom {
            display: flex;
            justify-content: center;
            margin-top: 56px;

            .btn-ok {
                height: 40px;
                background: #5C5EE4;
                border-radius: 4px;
                color: #FFF;
                margin-right: 8px;
            }

            .btn-no {
                height: 40px;
                border: 1px solid #D9DBE1;
                opacity: 1;
                border-radius: 4px;
            }
        }
    }

    // 支付弹窗样式 
    .pay-modal {
        .ant-modal-header {
            border-radius: .12rem .12rem 0 0;
            line-height: 1;
            padding: .4rem .3rem .3rem .3rem;
            border-bottom: 0;
            font-size: .18rem;
            color: #040E34;
        }

        .ant-modal-content {
            width: 6.4rem !important;
            border-radius: .12rem;
        }

        .ant-modal-body {
            padding-top: 0;
            padding-bottom: .4rem;
        }

        .ant-modal-footer {
            text-align: center;
            border-top: 0;
            padding: .16rem 0 .38rem;
        }

        .ant-btn {
            width: .88rem;
            line-height: .4rem;
            height: .4rem;
            border-radius: .04rem;
            padding: 0;
            font-size: .14rem;

            span {
                line-height: 1;
            }

            &.sure {
                background: #5C5EE4;
                color: #fff;
            }

            &.cancel {
                &:hover {
                    background: #5C5EE4;
                    color: #fff;
                }
            }
        }

        .cloud-pay-box {
            .item {
                display: flex;
                justify-content: flex-start;

                &.first,
                .ant-select-selector,
                .ant-select-selection-item {
                    line-height: .44rem;
                    height: .44rem;
                    font-size: .14rem;
                    border-radius: .04rem;
                }

                .ant-select-selection-item {
                    color: #9696A3;
                }

                &.first {
                    margin-bottom: .52rem;
                }

                &.last {
                    margin-top: .32rem;
                }
            }

            .ant-select {
                width: 5rem;
                display: block;
                height: .44rem;
                line-height: .44rem;
            }

            .l-text {
                width: .8rem;
                font-size: .13rem;
                color: #040E34;
            }

            .ant-checkbox-wrapper {
                margin-bottom: .24rem;
                line-height: 1;
            }

            .order-num {
                font-size: .14rem;
                color: #040E34;
                font-weight: 400;
            }

            .pay-num {
                font-size: .16rem;
                color: #FFA800;
                line-height: 1;

                p {
                    margin-bottom: 0;
                }

                .paid {
                    font-weight: 500;
                    margin-bottom: .1rem;
                }

                .balance {
                    font-size: .13rem;
                    margin-left: -.04rem;
                }
            }

            .pay-html {
                width: 100%;
                margin-left: 0;
                border: none;
                display: flex;
                justify-content: center;
            }
        }
    }

}
@primary-color: #5C5EE4;