.disk {
    font-family: Source Han Sans CN;
    font-size: 14px;
    font-weight: 400;

    .disk-yes {
        margin-top: 26px;

        .main_table {

            .ant-table-thead>tr>th {
                background: #F2F2FD;
                color: #9696A3;
                height: 13px;
                font-size: 13px;
                font-family: Source Han Sans CN;
                font-weight: 400;
            }

            .ant-table-tbody>tr {
                height: 0.6rem;
            }

            .ant-table-cell::before {
                height: 0px !important;
            }

            .more {
                font-size: 18;
                font-family: Microsoft YaHei;
                .more-text {
                    color: #5C5EE4;
                }

                .more-operation {
                    position: absolute;
                    background: #FFFFFF;
                    top: 20px;
                    left: 0;
                    display: none;
                    border-radius: 8px;
                    padding: 6px 10px;
                    border: 1px solid #eeeeee;
                    z-index: 100;
                    width: 50px;
                }

                &:hover .more-operation {
                    display: block;
                }

                &:hover .more-text {
                    display: none;
                }
            }


        }
    }

    .disk-no {
        width: 95%;
        height: 278px;
        border: 1px solid #D9DBE1;
        border-radius: 4px;
        margin: 1rem auto;
        display: flex;
        flex-direction: column;
        align-items: center;

        .disk-no-img {
            height: 40px;
            margin-top: 60px;
        }

        .disk-no-text {
            color: #525666;
            margin-top: 24px;
        }

        .disk-no-btn {
            border: none;
            background: #F0F0FD;
            color: #5C5EE4;
            font-weight: 500;
            margin-top: 26px;
        }
    }

    .create-snapshot {
        .top {
            .title {
                font-size: 18px;
                font-weight: 500;
                color: #040E34;
            }
        }

        .main {
            margin-top: 40px;

            .form {
                .row{
                    margin-top: 24px;

                    .ant-input,.ant-select{
                        width: 90%;
                        background: #F2F2F7;
                    }
                    .ant-select-selector{
                        background: #F2F2F7;
                    }
                    .label{
                        height: 100%;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        font-size: 13px;
                        color: #040E34;
                    }
                }
            }
        }

        .footer {
            display: flex;
            flex-direction: row-reverse;
            margin-top: 48px;

            .cancel-btn {
                height: 40px;
                background: #F2F2F7;
                border-radius: 4px;
                border: none;
                color: #525666;
            }

            .sub-btn {
                height: 40px;
                background: #696AE6;
                border-radius: 4px;
                color: #FFFFFF;
                margin-right: 8px;
            }
        }
    }
}

.ssh-cancel-button {
    background: #fff !important;
    color: #525666 !important;
    border: 1px solid #D9DBE1 !important;
}

.ssh-icon {
    color: #F64E60 !important;
    border-color: #F64E60 !important;
}

.refund-modal {
    width: 6.4rem !important;
    font-family: 'Source Han Sans CN';
  
    .ant-modal-body {
      padding: .4rem .3rem;
    }
  
    .modal-title {
      font-size: .18rem;
      color: #040E34;
      line-height: 1;
      margin-bottom: .3rem;
    }
  
    .config-item {
      margin-bottom: .2rem;
      display: flex;
  
      &:first-child {
        .row-r {
          padding: .25rem .2rem;
          font-size: .13rem;
          color: #9696A3;
        }
  
        .l-tit {
          margin-right: .2rem;
        }
  
        .item {
          display: flex;
          line-height: .14rem;
          margin-bottom: .2rem;
  
          &:last-child {
            margin-bottom: 0;
          }
  
          p {
            margin-bottom: 0;
          }
        }
  
        .r-text {
          p {
            margin-bottom: .1rem;
  
            &:last-child {
              margin-bottom: 0;
            }
          }
        }
      }
  
    }
  
    .row-l {
      line-height: .4rem;
      font-size: .14rem;
      color: #040E34;
      min-width: .8rem;
    }
  
    .row-r {
      border-radius: .04rem;
      background: #F2F2F7;
      flex: 1;
    }
  
    textarea,
    .ant-select-selector {
      background: #F2F2F7 !important;
      resize: none;
      border-color: #F2F2F7 !important;
    }
  
    .money {
      align-items: center;
  
      .row-l,
      .row-r {
        line-height: 1;
      }
    }
  
    .modal-footer {
      text-align: center;
  
      .ant-btn {
        line-height: .4rem;
        height: .4rem;
        min-width: .8rem;
        padding: 0 .16rem;
        border-radius: .04rem;
        margin: 0 .04rem;
      }
  
      .sure {
        background: #5C5EE4;
        color: #fff;
        border-color: #5C5EE4;
      }
  
      .cancel {
        border-color: #D9DBE1;
        color: #525666;
      }
    }
  
    .ant-select-selection-item {
      font-size: .14rem;
      color: #040E34;
    }
  }
@primary-color: #5C5EE4;