.manage .ant-row {
  margin-bottom: 0.3rem;
}
.power-modal {
  width: auto !important;
}
.power-modal.password-modal .ant-modal-content {
  width: 6.4rem;
}
.power-modal .ant-modal-header {
  border-radius: 0.12rem 0.12rem 0 0;
  border-bottom: 0;
  padding: 0.4rem 0.3rem 0.32rem 0.3rem;
}
.power-modal .ant-modal-title {
  font-size: 0.18rem;
  color: #040E34;
  line-height: 1;
  font-weight: 400;
}
.power-modal .ant-modal-content {
  width: 4.8rem;
  border-radius: 0.12rem;
  margin: 0 auto;
}
.power-modal .ant-modal-body {
  padding: 0 0.3rem 0.32rem 0.3rem;
}
.power-modal .ant-modal-body .ant-form-item {
  margin-bottom: 0.3rem;
}
.power-modal .ant-form-item,
.power-modal .ant-input {
  height: auto;
  line-height: 0.4rem;
}
.power-modal .ant-input-disabled {
  border: none;
}
.power-modal .ant-input {
  border-radius: 0.04rem;
  font-size: 0.13rem;
  color: #9696A3;
  padding: 0 0.16rem;
}
.power-modal .ant-col label {
  min-width: 0.62rem;
  font-size: 0.14rem;
  color: #040E34;
}
.power-modal .tip {
  font-size: 0.13rem;
  color: #040E34;
  margin-bottom: 0;
  line-height: 1;
}
.power-modal .force-off {
  margin-top: 0.1rem;
}
.power-modal .force-off label {
  font-size: 0.13rem;
  color: #525666;
}
.power-modal .force-off .ant-form-item-control-input {
  line-height: 1;
  min-height: auto;
}
.power-modal .force-off .ant-checkbox,
.power-modal .force-off .ant-checkbox-inner {
  width: 0.16rem;
  height: 0.16rem;
  border-radius: 0.04rem;
}
.power-modal .ant-form-item-explain-error {
  font-size: 0.12rem;
  margin-top: 0.05rem;
  color: #F64E60;
}
.power-modal .opt-btn {
  text-align: center;
  margin-bottom: 0 !important;
  margin-top: 0.57rem;
}
.power-modal .opt-btn .ant-btn {
  padding: 0 0.16rem;
  border-radius: 0.04rem;
  font-size: 0.14rem;
  line-height: 0.4rem;
  height: 0.4rem;
  margin: 0 0.04rem;
  letter-spacing: -0.02rem;
}
.power-modal .opt-btn .ant-btn span {
  height: 0.4rem;
}
.power-modal .opt-btn .ant-btn.sure {
  background: #5C5EE4;
  color: #fff;
  border-color: #5C5EE4;
}
.power-modal .opt-btn .ant-btn.cancel {
  border-color: #D9DBE1;
  color: #525666;
}
.power-modal .opt-btn .ant-btn.cancel:hover {
  background: #5C5EE4;
  color: #fff;
}
.power-modal .small-tip {
  font-size: 0.12rem;
  color: #9696A3;
  margin-top: -0.25rem;
  margin-bottom: 0.35rem;
}
.power-modal .c-btn {
  line-height: 0.44rem;
  height: 0.44rem;
  padding: 0 0.24rem;
  border-radius: 0.04rem;
  background: #F0F0FD;
  font-size: 0.14rem;
  color: #5C5EE4;
  display: inline-block;
  width: auto;
  cursor: pointer;
  transition: all 0.3s;
}
.power-modal .c-btn:hover {
  background: #5C5EE4;
  color: #fff;
}
/* password-modal */
.password-modal .ant-input {
  width: 3.92rem;
  line-height: 0.44rem;
  margin-right: 0.12rem;
}
.password-modal .ant-form-item {
  line-height: 0.44rem;
}
.password-modal .ant-col label {
  min-width: 0.72rem;
}
.password-modal .force-off {
  line-height: 1.2;
}
.password-modal .force-off label {
  height: 0.22rem;
  color: #040E34;
  font-size: 0.13rem;
}
.password-modal .pas-box {
  display: flex;
}
/* rescue-form */
.rescue-form .ant-select,
.rescue-form .ant-input {
  width: 3.6rem;
  line-height: 0.44rem;
  height: 0.44rem;
}
.rescue-form .ant-row .ant-select-selector,
.rescue-form .ant-row .ant-select-selection-item {
  line-height: 0.42rem;
  height: 0.44rem;
  border-radius: 0.04rem;
}
.rescue-form .ant-row .ant-select-selector {
  padding: 0 0.16rem;
}
.rescue-form .ant-col label {
  width: 1rem;
}
/* reinstall-modal */
.reinstall-modal {
  width: 6.4rem !important;
}
.reinstall-modal .dis-none {
  display: none;
}
.reinstall-modal .ant-modal-content {
  width: 6.4rem !important;
}
.reinstall-modal .ant-input-group-compact {
  display: flex;
}
.reinstall-modal .c-btn {
  white-space: nowrap;
}
.reinstall-modal .pay-box {
  padding: 0.12rem 0.2rem;
  background: #FFF4DE;
  border-radius: 0.04rem;
  font-size: 0.16rem;
  color: #525666;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #FFA800;
}
.reinstall-modal .go-pay {
  padding: 0.13rem 0.16rem;
  background: #FEB63B;
  color: #fff;
  font-size: 0.14rem;
  display: inline-block;
  border-radius: 0.04rem;
  line-height: 1;
  cursor: pointer;
}
.reinstall-modal .ant-col label {
  min-width: 1px !important;
  width: auto;
}
.reinstall-modal .ant-col label::after {
  margin: 0;
}
.reinstall-modal .ant-form-item {
  line-height: 0.44rem;
}
.reinstall-modal #port_num {
  margin-left: 0.2rem;
}
.reinstall-modal .ant-row .ant-select,
.reinstall-modal .ant-row .ant-select-selection-item,
.reinstall-modal .ant-row .ant-select-selector,
.reinstall-modal .ant-row .ant-input {
  line-height: 0.44rem !important;
  height: 0.44rem;
  border-radius: 0.04rem;
}
.reinstall-modal .ant-row .ant-select .ant-select-selector,
.reinstall-modal .ant-row .ant-select-selection-item .ant-select-selector,
.reinstall-modal .ant-row .ant-select-selector .ant-select-selector,
.reinstall-modal .ant-row .ant-input .ant-select-selector {
  border-radius: 0.04rem;
}
.reinstall-modal .ant-row .ant-select-selection-item {
  height: 0.42rem;
}
.reinstall-modal #pas_input {
  border-radius: 0.04rem;
}
.reinstall-modal .re-type {
  width: 1.47rem;
}
.reinstall-modal .system {
  width: 2rem;
  margin-left: 0.1rem;
}
.reinstall-modal .ant-form-item-required {
  width: 0.1rem;
}
.reinstall-modal .image-id {
  width: 2rem;
  margin-left: 0.12rem;
}
.reinstall-modal .ant-input-group-compact .ant-form-item {
  margin-bottom: 0;
}
.reinstall-modal .question {
  color: #DBDBDD;
  line-height: 0.44rem;
  margin-left: 0.08rem;
}
.reinstall-modal .pas-select {
  width: 2.85rem;
  margin-left: 0.15rem;
}
.reinstall-modal .c-btn {
  border: 1px solid #5C5EE4;
  padding: 0 0.16rem;
  color: #5C5EE4;
  background: #fff;
  margin-left: 0.1rem;
}
.reinstall-modal .ant-btn.disabled {
  opacity: 0.7;
}
#group-select .ant-select-item {
  display: flex;
  align-items: center;
}
.sys-icon {
  width: 0.24rem;
  height: 0.24rem;
  margin-right: 0.1rem;
}
@media screen and (max-width: 1600px) {
  .cloud-detail .manage .item {
    padding: 0.2rem;
  }
  .cloud-detail .manage .item .ant-select-selector {
    width: 1.2rem;
  }
}
