.DelDialog {
  transform-origin: center center !important;
  font-size: 0.2rem;
}
.DelDialog .ant-modal-header {
  border-top-left-radius: 0.2rem;
  border-top-right-radius: 0.2rem;
  border-bottom: 0;
  padding: 0.4rem 0.3rem 0.33rem 0.3rem;
}
.DelDialog .ant-modal-header .ant-modal-title {
  font-size: 0.18rem;
  color: #040E34;
}
.DelDialog .ant-modal-content {
  border-radius: 0.2rem;
}
.DelDialog .ant-modal-body {
  padding: 0 0.3rem;
  text-align: center;
}
.DelDialog .ant-modal-footer {
  padding: 0 0.3rem;
}
/* swal */
.swal2-modal {
  width: 4.8rem;
  border-radius: 0.2rem;
  position: relative;
  padding: 0.4rem 0.3rem;
}
.swal2-modal .del-tit {
  position: absolute;
  top: 0;
  width: 100%;
  text-align: left;
  line-height: 1;
  font-size: 0.18rem;
  color: #040E34;
  padding: 0.4rem 0.3rem;
  font-weight: 400;
}
.swal2-modal .del-icon {
  width: 0.62rem;
  height: 0.62rem;
  border-color: #F64E60;
  color: #F64E60;
  border-width: 2px;
  box-sizing: border-box;
  margin: 0.57rem auto 0.25rem;
}
.swal2-modal .del-icon .swal2-icon-content {
  font-size: 2.2em;
}
.swal2-modal .swal2-html-container {
  font-size: 0.14rem;
  color: #525666;
  text-align: center;
  margin: 0;
  line-height: 0.24rem;
}
.swal2-modal .swal2-confirm,
.swal2-modal .swal2-cancel {
  line-height: 0.38rem;
  padding: 0 0.15rem;
  border-radius: 0.04rem;
  margin: 0 0.04rem;
  font-size: 0.14rem;
  box-shadow: none !important;
  box-sizing: border-box;
}
.swal2-modal .swal2-actions {
  margin-top: 0.5rem;
}
.swal2-modal .swal2-confirm {
  background: #5C5EE4;
  border: 1px solid #5C5EE4;
}
.swal2-modal .swal2-cancel {
  background: #fff;
  color: #525666;
  border: 1px solid #D9DBE1;
}
