.cloud-network {
  height: auto;

  .com-card {
    padding: 0.56rem 0.3rem 1.95rem 0.3rem;
  }

  .box {
    margin-bottom: 0.5rem;

    .war-icon {
      width: .32rem;
    }

    .cloud-card {
      width: 100%;
      padding: 0.26rem 0.3rem;

      .chart_title {
        font-size: 0.16rem;
        color: #040e34;
        font-weight: bold;
        line-height: 1;
        margin-bottom: 0;
      }

      .echarts {
        height: 100%;
        width: 100%;
      }
    }

    .chartbox {
      height: 5rem;

      .chart_type {
        display: flex;
        justify-content: flex-end;
        margin-top: 10px;

        .chart_type_item {
          width: 0.64rem;
          height: 0.32rem;
          line-height: 0.32rem;
          background: #fff;
          border-radius: 0.04rem;
          color: #525666;
          text-align: center;
          cursor: pointer;
        }

        .active {
          background-color: #42c2fb;
          color: #fff;
        }
      }
    }

    .leftnet {
      background-color: #f2f2f7;
      height: 100%;

      .net_item {
        color: #9696a3;
        margin-bottom: 0.2rem;
      }
    }

    .security {
      border: 1px solid #f2f2f2;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding: 0.4rem;
      text-align: center;

      .nodata {
        color: #9696a3;
        line-height: 1;
        margin-top: .15rem;
      }

      .Outlined {
        font-size: 0.4rem;
        color: #9696a3;
      }

      .filename {
        color: #5c5ee4;
      }
    }
  }

  .iptit {
    display: flex;
    justify-content: space-between;
  }

  .changenet {
    color: #5c5ee4;
    cursor: pointer;
    margin-left: 0.05rem;
  }

  .com-tit {
    color: #040e34;
    line-height: 1;
    margin-bottom: 0.32rem;

    .tit {
      margin-bottom: 0;
      font-size: 0.18rem;
      font-weight: 500;
      color: #040e34;
      font-family: "Source Han Sans CN";
    }

    .subtit {
      margin-top: 0.2rem;
      font-size: 0.14rem;
      color: #040e34;
    }

    .sub-button {
      height: 40px;
      background: #696ae6;
      opacity: 1;
      border-radius: 4px;
      font-size: 14px;
      font-weight: 400;
      border: none;
    }
  }

  .cloud-tab {
    border: 1px solid #f2f2f2;
  }
}

.sec_btn {
  background-color: #f0f0fd;
  color: #5c5ee4;
  border: none;
}

.modal {
  .subtit {
    color: #9696a3;
    font-size: o.12rem;
  }

  .modal_con {
    margin-top: 20px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-evenly;
  }

  .ant-modal-header {
    border-bottom: none !important;
  }

  .ant-modal-footer {
    border-top: none !important;
    text-align: center;
  }
}
.security_box{
  .ant-btn{
    margin-top: .2rem;
    border-radius: .04rem;
    &:hover{
      background: #696AE6;
      color: #fff;
    }
  }
}
.power-modal {
  .ant-modal-body {
    .ant-select,
    .ant-row,
    .ant-select-selector,
    .ant-form-item,
    .ant-col,
    .ant-col label,
    .ant-select-selection-item {
      height: .44rem !important;
      line-height: .44rem;
    }
    .ant-select-selector{
      border-radius: .04rem;
    }
  }
}
@primary-color: #5C5EE4;