.chart {
  width: 77.945619%;
  margin: 0 auto 0.2rem;
  .cloud-card {
    width: 100%;
    height: 5rem;
    padding: 0.26rem 0.3rem;
    .chart_title {
      font-size: 0.16rem;
      color: #040e34;
      font-weight: bold;
      line-height: 1;
      margin-bottom: 0;
    }
    .chart_type {
      display: flex;
      justify-content: flex-end;
      .chart_type_item {
        width: 0.64rem;
        height: 0.32rem;
        line-height: 0.32rem;
        background: #fff;
        border-radius: 0.04rem;
        color: #525666;
        text-align: center;
        cursor: pointer;
      }
      .active {
        background-color: #42c2fb;
        color: #fff;
      }
    }
    .echarts {
      height: 100%;
      width: 100%;
      // height: 4rem;
      // width: 5.5rem;
    }
  }
}

@primary-color: #5C5EE4;