.backup-snapshot {
    font-family: Source Han Sans CN;
    font-size: 14px;
    font-weight: 400;

    .snapshot-yes {
        margin-top: 26px;
        width: 95%;
        margin: 0.26rem auto 0;

        .ant-pagination {
            float: right;
            margin-right: 0;
            margin-top: 0.24rem;

            .ant-pagination-total-text {
                display: none;
            }

            .ant-pagination-options {
                display: none;
            }
        }

        .snapshot-yes-top {
            display: flex;
            justify-content: space-between;
            margin-bottom: 0.12rem;

            .left {
                .title {
                    font-size: 18px;
                    font-weight: 500;
                    color: #040E34;
                }

                .text {
                    font-size: 13px;
                    color: #9696A3;
                    margin-left: 6px;
                }
            }

            .right {
                .btn-updown {
                    height: 40px;
                    background: #F0F0FD;
                    border-radius: 4px;
                    color: #5C5EE4;
                    margin-right: 0.16rem;
                    border: none;
                }

                .btn-create {
                    height: 40px;
                    background: #5C5EE4;
                    border-radius: 4px;
                    color: #FFFFFF;
                }
            }
        }

        .main_table {
            border: 1px solid #D9DBE1;

            .ant-table-thead>tr>th {
                background: #F2F2FD;
                color: #9696A3;
                height: 13px;
                font-size: 13px;
                font-family: Source Han Sans CN;
                font-weight: 400;
                height: 40px;
            }

            .ant-table-tbody>tr {
                height: 60px;
            }

            .ant-table-cell::before {
                height: 0px !important;
            }

            .more {
                font-size: 18;
                font-family: Microsoft YaHei;

                .more-text {
                    color: #5C5EE4;
                }

                .more-operation {
                    position: absolute;
                    background: #FFFFFF;
                    top: 20px;
                    left: 0;
                    display: none;
                    border-radius: 8px;
                    padding: 6px 10px;
                    border: 1px solid #eeeeee;
                    z-index: 100;
                    width: 50px;
                }

                &:hover .more-operation {
                    display: block;
                }

                &:hover .more-text {
                    display: none;
                }
            }


        }
    }

    .snapshot-no {
        width: 95%;
        display: flex;
        flex-direction: column;
        margin: 0.26rem auto 0;

        .snapshot-no-title {
            font-size: 18px;
            font-weight: 500;
            line-height: 28px;
            color: #040E34;
        }

        .snapshot-no-main {
            height: 305px;
            border: 1px solid #D9DBE1;
            border-radius: 4px;
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-top: 0.24rem;

            .snapshot-no-img {
                height: 40px;
                margin-top: 60px;
            }

            .snapshot-no-text {
                color: #525666;
                margin-top: 24px;
            }

            .snapshot-no-money {
                line-height: 20px;
                color: #5C5EE4;
                margin-top: 12px;
            }

            .snapshot-no-btn {
                border: none;
                background: #F0F0FD;
                color: #5C5EE4;
                font-weight: 500;
                margin-top: 26px;
            }
        }


    }

    .backup-yes {
        padding-top: 88px;
        width: 95%;
        margin: 0.26rem auto 0;

        .ant-pagination {
            float: right;
            margin-right: 0;
            margin-top: 0.24rem;

            .ant-pagination-total-text {
                display: none;
            }

            .ant-pagination-options {
                display: none;
            }
        }

        .backup-yes-top {
            width: 100%;
            display: flex;
            justify-content: space-between;
            margin-bottom: 0.12rem;

            .left {
                display: flex;
                flex-direction: column;

                .title {
                    font-size: 18px;
                    font-weight: 500;
                    color: #040E34;
                }

                .text {
                    font-size: 13px;
                    color: #9696A3;
                    margin-top: 0.24rem;
                }
            }

            .right {
                display: flex;
                flex-direction: column-reverse;

                .btn-close {
                    height: 40px;
                    background: #F0F0FD;
                    border-radius: 4px;
                    color: #5C5EE4;
                    border: none;
                }
            }
        }

        .main_table {
            border: 1px solid #D9DBE1;

            .ant-table-thead>tr>th {
                background: #F2F2FD;
                color: #9696A3;
                height: 13px;
                font-size: 13px;
                font-family: Source Han Sans CN;
                font-weight: 400;
            }

            .ant-table-tbody>tr {
                height: 0.6rem;
            }

            .ant-table-cell::before {
                height: 0px !important;
            }

            .more {
                font-size: 18;
                font-family: Microsoft YaHei;

                .more-text {
                    color: #5C5EE4;
                }

                .more-operation {
                    position: absolute;
                    background: #FFFFFF;
                    top: 20px;
                    left: 0;
                    display: none;
                    border-radius: 8px;
                    padding: 6px 10px;
                    border: 1px solid #eeeeee;
                    z-index: 100;
                    width: 50px;
                }

                &:hover .more-operation {
                    display: block;
                }

                &:hover .more-text {
                    display: none;
                }
            }


        }
    }

    .backup-no {
        width: 95%;
        display: flex;
        flex-direction: column;
        margin: 0.6rem auto 0;

        .backup-no-title {
            font-size: 18px;
            font-weight: 500;
            line-height: 28px;
            color: #040E34;
        }

        .backup-no-main {
            height: 305px;
            border: 1px solid #D9DBE1;
            border-radius: 4px;
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-top: 0.24rem;
            margin-bottom: 0.3rem;

            .backup-no-img {
                height: 40px;
                margin-top: 60px;
            }

            .backup-no-text {
                color: #525666;
                margin-top: 24px;
            }

            .backup-no-money {
                line-height: 20px;
                color: #5C5EE4;
                margin-top: 12px;
            }

            .backup-no-btn {
                border: none;
                background: #F0F0FD;
                color: #5C5EE4;
                font-weight: 500;
                margin-top: 26px;
            }
        }
    }

    .create-snapshot {
        .top {
            .title {
                font-size: 18px;
                font-weight: 500;
                color: #040E34;
            }
        }

        .main {
            margin-top: 40px;

            .form {
                .row {
                    margin-top: 24px;

                    .ant-input,
                    .ant-select {
                        width: 90%;
                        background: #F2F2F7;
                    }

                    .ant-select-selector {
                        background: #F2F2F7;
                    }

                    .label {
                        height: 100%;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        font-size: 13px;
                        color: #040E34;
                    }
                }
            }
        }

        .footer {
            display: flex;
            // flex-direction: row-reverse;
            justify-content: center;
            margin-top: 48px;

            .cancel-btn {
                height: 40px;
                background: #F2F2F7;
                border-radius: 4px;
                border: none;
                color: #525666;
            }

            .sub-btn {
                height: 40px;
                background: #696AE6;
                border-radius: 4px;
                color: #FFFFFF;
                margin-right: 8px;
            }
        }
    }
}

.swal2-popup {
    font-size: 0.2rem;
}
@primary-color: #5C5EE4;